.embed-responsive {
  position: relative;
  display: block;
  width: 100%;
  padding: 0;
  overflow: hidden;

  &::before {
    content: '';
    display: block;
    padding-top: percentage(9 / 16); //       16/9
  }

  // embed,
  img,
  iframe,
  video {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
  }

  img {
    display: block;
    object-fit: cover;
    object-position: center;
  }



  &--4-3 { //     4/3

    &::before {
      padding-top: percentage(3 / 4);
    }
  }

  &--1-1 {

    &::before {
      padding-top: percentage(1 / 1);
    }
  }

  &--skewed {

    &::before {
      padding-top: percentage(1 / 2.23);
    }
  }
}
